@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&family=Ubuntu:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{top:0;position:-webkit-sticky;position:sticky;display:flex;align-items:center;justify-content:space-between;padding:20px;background-color:#4e55ff;box-shadow:0px 0px 20px 0px rgba(0,0,0,0.2)}header .header__nav__link{color:#fff;padding:20px}header .header__nav__button{color:#fff;border:2px solid white;margin-left:20px;padding:7px 34px;background-color:#4e55ff;border-radius:20px}header .header__nav__button:hover{color:#4e55ff;background-color:#fff}

:root{--rot: 0deg}.background-container{background-color:#060d25;position:absolute;overflow:hidden;z-index:-1;opacity:1;top:0;width:100%;height:100%}.background-container .bg-div{left:50%;top:50%;position:absolute;height:90vw;width:90vw;border:3px solid #4e54ff25;transform:translate(-50%, -50%) rotate(0deg) scale(0.97);transform:translate(-50%, -50%) rotate(var(--rot)) scale(0.97)}

.hero{padding-top:35vh;height:calc(65vh - 77px)}.hero__greetings,.hero__role{color:#4e55ff}.hero__name,.hero__role,.hero__intro{margin:0}.hero__intro{margin-top:20px}.hero__bottom-gradient{z-index:-1;position:absolute;bottom:0;width:100%;height:150px;background:linear-gradient(0deg, #060d25 0%, #060d25 44%, rgba(6,13,37,0) 100%)}

.about{display:grid;padding:30vh 0;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr;grid-gap:30px;gap:30px;grid-template-areas:"about__info about__info about__picture about__links"}.about__info{grid-area:about__info}.about__info__title{margin-top:-20px;padding-right:20px;margin-bottom:0;display:inline-block}.about__info__line{margin:0 0 0 190px;color:#4e55ff}.about__picture img{max-width:90%;margin:5%}.about__links{text-align:center;grid-area:about__links;border:1px solid #4e55ff;border-radius:20px;background:#0a1334}.about__links__more{margin-bottom:50px}.about__top-gradient{z-index:-1;position:relative;top:0;width:100%;height:250px;background:linear-gradient(180deg, #060d25 0%, #0a1334 44%, rgba(6,13,37,0) 100%)}

@-webkit-keyframes move-it{0%{background-position:0 0;background-position:initial}100%{background-position:100px 0px}}@keyframes move-it{0%{background-position:0 0;background-position:initial}100%{background-position:100px 0px}}@-webkit-keyframes hue-mutate{0%,100%{-webkit-filter:hue-rotate(-50deg);filter:hue-rotate(-50deg)}50%{-webkit-filter:hue-rotate(50deg);filter:hue-rotate(50deg)}}@keyframes hue-mutate{0%,100%{-webkit-filter:hue-rotate(-50deg);filter:hue-rotate(-50deg)}50%{-webkit-filter:hue-rotate(50deg);filter:hue-rotate(50deg)}}@-webkit-keyframes bright-mutate{0%,100%{-webkit-filter:brightness(1);filter:brightness(1)}50%{-webkit-filter:brightness(3);filter:brightness(3)}}@keyframes bright-mutate{0%,100%{-webkit-filter:brightness(1);filter:brightness(1)}50%{-webkit-filter:brightness(3);filter:brightness(3)}}@keyframes move-it{0%{background-position:0 0;background-position:initial}100%{background-position:100px 0px}}@-webkit-keyframes mainframe-fill{0%,100%{fill:#fff}33%{fill:#0a1334}66%{fill:#2c385d}}@keyframes mainframe-fill{0%,100%{fill:#fff}33%{fill:#0a1334}66%{fill:#2c385d}}html{background:repeating-linear-gradient(45deg, #0a1334, #0a1334 5%, #060d25 5%, #060d25 10%);background-size:100px 100px;-webkit-animation:move-it 3s linear infinite;animation:move-it 3s linear infinite}.skills{background:#4e55ff;padding:20vh 0;text-align:center}.skills__title{display:inline-block;padding:20px 15px;margin-top:-20px;background-color:#4e55ff}.skills__line{color:#2c385d;margin-bottom:-25px}.skills__traits{display:grid;padding:20px 0;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr;grid-gap:30px;gap:30px;grid-template-areas:"skills__traits__1 skills__traits__2 skills__traits__3 skills__traits__4"}.skills__traits__icon{fill:#fff;width:100%;max-width:140px}.skills__traits__1{grid-area:skills__traits__1}.skills__traits__2{grid-area:skills__traits__2}.skills__traits__3{grid-area:skills__traits__3}.skills__traits__4{grid-area:skills__traits__4}.skills__grid{display:grid;padding:60px 0;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr;grid-gap:30px;gap:30px;grid-template-areas:"skills__grid__front skills__grid__front skills_grid_design skills_grid_design"}.skills__grid__front{grid-area:skills__grid__front}.skills__grid__design{grid-area:skills_grid_design}

